



































import Vue from 'vue';

export default Vue.extend({
  props: {
    checkMode: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      signature: '',
      form: {},
      overEventTime: false,
      overRegistrationTime: false,
    };
  },
  computed: {
    eventInfo() {
      return this.$store.state.eventInfo;
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.signature = this.$store.state.form.signature;
      this.form = this.$store.state.form;

      const eventTimestamp = Date.parse(this.eventInfo.eventEndDate);
      const registrationTimestamp = Date.parse(this.eventInfo.registrationEndDate);

      const currentTimestamp = Date.now();
      if (eventTimestamp < currentTimestamp) {
        this.overEventTime = true;
      }
      if (registrationTimestamp < currentTimestamp) {
        this.overRegistrationTime = true;
      }
    },
    saveInfo(move: string) {
      if (this.signature !== '') {
        this.$store.commit('saveSignature', this.signature);
        if (move === 'next') {
          this.$router.push('personalInformation');
        }
      } else {
        // eslint-disable-next-line no-alert
        alert('請先填寫名字，謝謝');
      }
    },
  },
});
